import React, { useState } from "react";
import {
  Back,
  CommentCard,
  CommentFlex,
  CommentUsers,
  MyCommentIn,
  MyComments,
  MyMessage,
  PageCont,
  PageFlex,
  PageImg,
  PageImg1,
  PageImg2,
  PageImg3,
  PageWrap,
  Popupcard,
  Popupcardflex,
  ScrollComment,
  TheyComments,
  UserComments,
} from "./forTelephone.js";
import { saveAs } from "file-saver";

import { Link } from "react-router-dom";
// import Dcity from "../Images/Dark city comp/dark-city10.jpg"
// import Ddark from "../Images/dark-comp/dark-comp4.jpg"
// import Dyear from "../Images/year comp/year-comp6.jpg"
// import Heart from "../icons/heart.png"
// import Like from "../icons/like.png"
import Likecomment from "../likecomment";

function ForTelephone() {
  const [Popup, setPopup] = useState({ popup: false, img: "", text: "" });
  const [Comment, setComment] = useState({ comment: false });
  const [MyComment, setMyComment] = useState([
    {
      img: "https://uybor.uz/borless/avtobor/img/user-images/no-avatar.png",
      comments: "good",
    },
    {
      img: "https://images.unsplash.com/photo-1535713875002-d1d0cf377fde?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MXx8dXNlcnxlbnwwfHwwfHw%3D&w=1000&q=80",
      comments: "Class Foto",
    },
    {
      img: "https://demo.nparoco.com/Vuexy/app-assets/images/profile/user-uploads/user-04.jpg",
      comments: "I Like foto",
    },
  ]);
  // const [UserComment, setUserComment] = useState([])

  const [OneImgComments, setOneImgComments] = useState([]);
  const [TwoImgComments, setTwoImgComments] = useState([]);
  const [ThreeImgComments, setThreeImgComments] = useState([]);
  const [OneImgLen, setOneImgLen] = useState({ numbers: 3 });
  const [TwoImgLen, setTwoImgLen] = useState({ numbers: 3 });
  const [ThreeImgLen, setThreeImgLen] = useState({ numbers: 3 });
  const [Like, setLike] = useState({ like: "far fa-heart" });
  const DataImg = [
    {
      index: 0,
      forImg:
        "https://firebasestorage.googleapis.com/v0/b/foto-e78e2.appspot.com/o/dark%20city%20tel%2Fcity-tel1.jpg?alt=media&token=e2a2d4f2-863a-4e95-8f30-0f5e1a91785c",
      text: "For Telephone ",
    },
    {
      index: 1,
      forImg:
        "https://firebasestorage.googleapis.com/v0/b/foto-e78e2.appspot.com/o/dark%20city%20tel%2Fdark-city.jfif?alt=media&token=434b4454-28ef-406c-b960-95a1b05f178b",
      text: " For Telephone",
    },
    {
      index: 2,
      forImg:
        "https://firebasestorage.googleapis.com/v0/b/foto-e78e2.appspot.com/o/dark%20city%20tel%2Fnight-city-tel2.jpg?alt=media&token=ab1fc3e0-735c-466f-9e00-d1383fe54a77",
      text: "For Telephone",
    },
  ];
  const [img, setimg] = useState({ foto: "", index: "" });
  const [TrueComment, setTrueComment] = useState({ comment: false });
  const PushComment = (e) => {
    e.preventDefault();
    const inputcomment = document.getElementById("mypushcomment").value;
    const inputImg =
      "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQ2nq3TykgvJ8D_NlG-5_C_cfxMDQz0u2EYlw&usqp=CAU";
    if (inputcomment === "") {
      return "nan";
    } else if (img.index === 0) {
      const PushComments = {
        img: inputImg,
        comments: inputcomment,
      };

      const NumberLen = OneImgLen.numbers + 1;
      setOneImgLen({ numbers: NumberLen });
      setTrueComment({ comment: true });
      OneImgComments.push(PushComments);
      // setUserComment([ PushComments])
      console.log("push");
      console.log(PushComments);
    } else if (img.index === 1) {
      const PushComments = {
        img: inputImg,
        comments: inputcomment,
      };
      const NumberLen = (TwoImgLen.numbers += 1);
      setTwoImgLen({ numbers: NumberLen });
      setTrueComment({ comment: true });
      TwoImgComments.push(PushComments);
      // setUserComment([ PushComments])
      console.log("push");
      console.log(PushComments);
    } else if (img.index === 2) {
      const PushComments = {
        img: inputImg,
        comments: inputcomment,
      };

      const NumberLen = (ThreeImgLen.numbers += 1);
      setThreeImgLen({ numbers: NumberLen });
      setTrueComment({ comment: true });
      ThreeImgComments.push(PushComments);
      // setUserComment([ PushComments])
      console.log("push");
      console.log(PushComments);
    }

    const remove = (document.getElementById("mypushcomment").value = "");
    console.log(MyComment.length);
  };
  // NumberLength()
  return (
    <PageWrap>
      {/* <PageFlex>
                <div>
                    <div className="cate">
                        For Telephone  
                    </div>
                  
                    <div className="right">
                        <Link to="category">
                            <img src={Right} alt="" />
                        </Link>
                    </div>
                </div>
            </PageFlex> */}
      <PageImg>
        {DataImg.map((person) => {
          return (
            <PageImg1 key={person.id}>
              <div
                onClick={() => {
                  setPopup({
                    popup: true,
                    img: person.forImg,
                    text: person.text,
                  });
                }}
                className="img"
              >
                <img
                  onClick={() => {
                    setimg({ foto: person.forImg, index: person.index });
                  }}
                  src={person.forImg}
                  alt=""
                />
              </div>

              <Link to={"/telp-photos"}>
                <p className="content">{person.text}</p>
              </Link>
            </PageImg1>
          );
        })}
      </PageImg>
      {Popup.popup ? (
        <>
          <Back
            onClick={() => {
              setPopup({ popup: false });
            }}
          >
            <div
              onClick={() => {
                setComment({ comment: false });
              }}
            ></div>
          </Back>
          <Popupcard>
            <div>
              <img src={img.foto} alt="" />
            </div>

            <Popupcardflex>
              <link
                rel="stylesheet"
                href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.0.0/css/all.min.css"
                integrity="sha512-9usAa10IRO0HhonpyAIVpjrylPvoDwiPUiKdWk5t3PyolY1cOd4DSE0Ga+ri4AuTroPR5aQvXU9xC6qOPnzFeg=="
                crossorigin="anonymous"
                referrerpolicy="no-referrer"
              />
              <p>For Telephone</p>
              <button>
                <i style={{ color: "black" }} class="fas fa-arrow-down"></i>
              </button>
              <div
                onClick={() => {
                  setComment({ comment: true });
                }}
              >
                <i
                  onClick={() => {
                    setPopup({ popup: false });
                  }}
                  class="far fa-comments"
                ></i>
              </div>
            </Popupcardflex>
          </Popupcard>
        </>
      ) : (
        ""
      )}
      {Comment.comment ? (
        <>
          <Back
            onClick={() => {
              setComment({ comment: false });
            }}
          />
          <CommentCard>
            <CommentFlex>
              <div
                onClick={() => {
                  setComment({ comment: false });
                }}
                className="icon"
              >
                <i
                  onClick={() => {
                    setPopup({ popup: true });
                  }}
                  class="fas fa-arrow-left"
                ></i>
              </div>
              <div className="text">
                {img.index === 0 ? (
                  <>{OneImgLen.numbers}</>
                ) : (
                  <>
                    {img.index === 1 ? (
                      <>{TwoImgLen.numbers}</>
                    ) : (
                      <>{img.index === 2 ? <>{ThreeImgLen.numbers}</> : ""}</>
                    )}
                  </>
                )}{" "}
                Comments
              </div>
            </CommentFlex>
            <ScrollComment>
              <TheyComments>
                {MyComment.map(({ img, comments }) => (
                  <UserComments>
                    <div className="user">
                      <img src={img} alt="" />
                    </div>
                    <div className="message">
                      {comments} <Likecomment />
                    </div>
                  </UserComments>
                ))}
              </TheyComments>
              <MyComments>
                {
                  (TrueComment.comment = false ? (
                    <></>
                  ) : (
                    <>
                      {img.index === 0 ? (
                        OneImgComments.map(({ img, comments }, index) => (
                          <MyCommentIn>
                            <div className="user">
                              <img src={img} alt="" />
                            </div>
                            <div className="message">
                              {comments} <Likecomment />
                            </div>
                          </MyCommentIn>
                        ))
                      ) : (
                        <>
                          {img.index === 1 ? (
                            TwoImgComments.map(({ img, comments }, index) => (
                              <MyCommentIn>
                                <div className="user">
                                  <img src={img} alt="" />
                                </div>
                                <div className="message">
                                  {comments} <Likecomment />
                                </div>
                              </MyCommentIn>
                            ))
                          ) : (
                            <>
                              {img.index === 2
                                ? ThreeImgComments.map(
                                    ({ img, comments }, index) => (
                                      <MyCommentIn>
                                        <div className="user">
                                          <img src={img} alt="" />
                                        </div>
                                        <div className="message">
                                          {comments} <Likecomment />
                                        </div>
                                      </MyCommentIn>
                                    )
                                  )
                                : ""}
                            </>
                          )}
                        </>
                      )}
                    </>
                  ))
                }
              </MyComments>
            </ScrollComment>
            <MyMessage>
              <input
                id="mypushcomment"
                type="text"
                placeholder="Type Comment"
              />
              <button onClick={PushComment} class="fas fa-paper-plane"></button>
            </MyMessage>
          </CommentCard>
        </>
      ) : (
        ""
      )}
    </PageWrap>
  );
}

export default ForTelephone;
