import styled from "styled-components";

export const NavbarWrap = styled.nav`
position: fixed;
background: #b9ffb7;
z-index: 4;
transition:all 1s ease-out;
div{
    font-size: 50px;
    font-weight: bold;
    margin: 20px;
  
   
    border-radius: 50%;
    @media screen and (max-width:992px){
        font-size:45px;
    }
    @media screen and (max-width:768px){
        font-size:45px;
    }
    @media screen and (max-width:510px){
        font-size:30px;
        width:150px ;
        img{
            width:100% ;
        }
    }
    img{
        background: #b9ffb7;
        border-radius: 50%;
     cursor: pointer;
     padding-left: 10px;
    }
}
@media screen and(max-width:992px){
    position: fixed;
    
background: #b9ffb7;
z-index: 4;
transition:all 1s ease-out;
}
@media screen and(max-width:510px){
    position: fixed;
    bottom: 0;
background: #b9ffb7;
z-index: 4;
transition:all 1s ease-out;
}

`
export const Nav = styled.nav`
display: flex;
justify-content: center;
flex-direction: column;
align-items: center;
@media (max-width:510px){
    height:50px ;
}
div{
    position: absolute;
    right: 100px;
    top: 20%;
    width: 400px;
    @media screen and (max-width:1200px){
        width:380px ;
        img{
            width:100% ;
        }
    }
    @media screen and (max-width:992px){
        width:350px ;
        img{
            width:100% ;
        }
    }
    @media screen and (max-width:768px){
        width:350px ;
    
        img{
            width:100% ;
        }
    }
    @media screen and (max-width:510px){
        width:0;
    display: none;
        img{
            width:100% ;
        }
    }
    img{
        width: 100%;
        border-radius:0 ;
    }
}
ul{
    padding: 20px;
margin-top:-70px ;
@media screen and (max-width:1200px){
    margin-top:-20px ;
    margin-left:-90px ;
}
@media screen and (max-width:992px){
    margin-top:-30px ;
    margin-left:-70px ;
}
@media screen and (max-width:768px){
    margin-top:50px ;
    margin-left:-80px ;
}
@media screen and (max-width:510px){
    margin-top:-230px ;
    display: flex;
    margin-left:100px ;
}
    li{
        list-style: none;
        margin-top: 20px;
        padding: 20px;
        @media (max-width:510px){
        
            display: flex;
        }
        .home-icon{
            @media (max-width:510px){
              
                font-size:25px;
                color:white ;
            }
        }
        .beast-icon{
            @media (max-width:510px){
            color:white ;
                font-size:20px;
            }
        }
        .computer-icon{
            @media (max-width:510px){
                color:white ;
                font-size:20px;
            }
        }
        .telep-icon{
            @media (max-width:510px){
              color:white ;
                font-size:20px;
            }
        }
      a{
          text-decoration: none;
          color: black;
          font-size: 25px;
          font-weight: bold;
          
      }
    }
}
`

export const NavbarText = styled.div`
font-size: 50px;
color: red;
background: red;
width: 100px;
height: 50px;
`
export const NavbarImg = styled.div`
position: fixed;
right: 5%;
top: 5%;
z-index: 5;
width: 30px;
color: white;
cursor: pointer;
img{
    width: 100%;
}
@media screen and (max-width:992px){
    right: 5%;
    top: 3%;
}
@media screen and (max-width:768px){
    right: 5%;
    top: 2%;
}
@media screen and (max-width:660px){
    right: 5%;
    width:30px ;
    top: 2%;
}

`