import styled from "styled-components";

export const PageWrap = styled.div`

/* z-index: 2; */
width:99vw ;
/* padding: 40px 100px; */
background: #FAB58C;
@media (max-width:1200px){
    width:100vw ;
}
@media (max-width:992px){
    width:1130px ;
    margin-top:-50px ;
}
@media (max-width:768px){
    width:1130px ;
}
@media (max-width:660px){
    width:1130px ;
}
@media (max-width:510px){
    width:1130px;
}
@media (max-width:415px){
    width:100vw ;
}
@media (max-width:395px){
    width:100vw ;
}

`

export const PageFlex = styled.div`
div{
    display: flex;
    justify-content: space-between;
    .cate{
        font-size: 25px;
        color: black;
font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    }


}`
export const PageImg = styled.div`
display: flex;
margin:0 15px 0 15px ;
@media (max-width:510px){
display: block;
}
justify-content: space-between;
`
export const PageImg1 = styled.div`
position: relative;
width:33%;
/* z-index:2 ; */
cursor: pointer;
transition:0.3s;
--background:#FFFFFF;
--text:black;
padding-bottom:15px;
@media (max-width:510px){
width:100% ;
}
.content{
    position:relative ;
    position:absolute ;
    bottom:20px;
    padding:15px ;
    cursor: pointer;
    border:1px solid black ;
    border-radius:30px  ;
    left:10px ;
    color:white ;
&:hover{
    background:white;
    color:black ;  
}   
}
.icon{
             z-index:0;
            position: absolute;
            top:20px;
            left:20px;
            border-radius:100%;
            width:0rem;
            height:0rem;
            transform: translate(-30%, -30%);
            transition: 0.25s cubic-bezier(0.25, 0, 0, 1);
   
    i{
        z-index: 0;
        display: grid;
                place-items: center;
                position: absolute;
                width:0;
                font-size: 0;
                height:0;
                border:none;
                border-radius:100%;
                background:var(--background);
                color:var(--text);
                transform: translate(-50%, -50%);
                cursor: pointer;
                transition: .25s cubic-bezier(0.25, 0, 0, 1);
                box-shadow:0 0 0rem -.25rem var(--background);
                &:hover{
                  
                    box-shadow:0 0 1rem -.25rem var(--background);
                }
    }
}

.img{
    width:100% ;
 
    height:600px;
   position: relative;
overflow:hidden ;
    img{
        height:100% ;
        transition:0.3s ;
        object-fit:cover;
    width: 100%;
    &:hover{
        width:100% ;
    transform:scale(1.1) ;
   }
}
}

`
