import React from 'react';
import { useState } from 'react';
import { DontLike, Like, LikeCard } from './like';

function Likecomment() {
    const [CommentLike,setCommentLike]=useState({like:"far fa-thumbs-up",number:0})
    const [CommentDontLike,setCommentDontLike]=useState({like:"far fa-thumbs-down",number:0})
  return (
      <LikeCard>
            <link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.15.3/css/all.css" integrity="sha384-SZXxX4whJ79/gErwcOYf+zWLeJdY/qpuqC4cAa9rOGUstPomtqpuNWT9wdPEn2fk" crossorigin="anonymous"></link>
          <Like>
            
              <button  onClick={() => { setCommentLike({ like: `${CommentLike.like == "far fa-thumbs-up" ? "fas fa-thumbs-up" : "far fa-thumbs-up"}`,number:`${CommentLike.number==0?+1:0}` }) }} class={CommentLike.like}></button><span>{CommentLike.number}</span>
              
          </Like>
          <DontLike>
          <button onClick={() => { setCommentDontLike({ like: `${CommentDontLike.like == "far fa-thumbs-down" ? "fas fa-thumbs-down" : "far fa-thumbs-down"}` }) }} > {
              
          } <i onClick={()=>{setCommentLike({like:"far fa-thumbs-up",number:0})}} class={CommentDontLike.like}></i> </button>
          </DontLike>
      </LikeCard>
  )
}

export default Likecomment;
