
import styled from "styled-components";

export const LikeCard=styled.div`
display: flex;
margin-left: 70px;
margin-top: 2px;
`
export const Like=styled.div`
font-size: 15px;
button{
    cursor: pointer;
    border: 0;
    background: transparent;
}
span{
    margin-left: 5px;
}
`
export const DontLike=styled.div`
font-size: 15px;
cursor: pointer;
margin-left: 10px;
margin-top: 1.5px;
button{
    cursor: pointer;
    border: 0;
    background: transparent;
}
`