import styled from "styled-components";

import Img from "../Images/music.png"
export const BodyWrap=styled.div`
width: 99vw;

height: 100vh;
background-attachment: fixed;
background-size: 100%;
background-repeat: no-repeat;
background-image: url('https://firebasestorage.googleapis.com/v0/b/foto-e78e2.appspot.com/o/back-img.jpg?alt=media&token=da5c1b0e-ff59-43b8-b384-3021163ed3be');
.divSphoto{
    color:#00ff9d;
font-weight: bold;
font-size: 80px;
position: absolute;
left: 15%;
top: 48%;
@media (max-width:992px){
    font-size:70px;
    top:40% ;
}
@media (max-width:768px){
    font-size:50px;
    position:absolute ;
    top:-50% ;
    left:18% ;
    margin-top:-5% ;
}
@media (max-width:1200px){
    top:30% ;
}
@media (max-width:660px){
    top:35% ;
    left:35% ;
}
@media (max-width:510px){
    top:35% ;
    font-size:40px ;
    left:15% ;
}
@media (max-width:415px){
    top:35% ;
    font-size:50px ;
    left:45% ;
}

}
@media (max-width:1200px){
    width:100%;
    background-attachment: fixed;
background-size: 100%;
background-repeat: no-repeat;
    height:64vh ;
    background-image: url('https://firebasestorage.googleapis.com/v0/b/foto-e78e2.appspot.com/o/back-img.jpg?alt=media&token=da5c1b0e-ff59-43b8-b384-3021163ed3be');
}
@media (max-width:992px){
    width:1130px;
    background-attachment: fixed;
background-size: 100%;
background-repeat: no-repeat;
    height:64vh ;
    background-image: url('https://firebasestorage.googleapis.com/v0/b/foto-e78e2.appspot.com/o/back-img.jpg?alt=media&token=da5c1b0e-ff59-43b8-b384-3021163ed3be');
}
@media (max-width:768px){
    width:1130px;
    height:64vh ;
}
@media (max-width:510px){
    width:1100px;
    background-size:100% ;
    height:64vh ;
}
@media (max-width:415px){
    width:1125px;
    background-size:100% ;
    height:64vh ;
}
`
export const BackOn=styled.div`
p{
    text-align:center ;
    color: white;
  
}
`
export const BodyImg=styled.div`
width: 400px;

height: 100px;
position: absolute;
right:15%;
top: 25%;

z-index: 1;
overflow-x: initial;
img{
    overflow-x: initial;
    width: 100%;
}
@media (max-width:992px){
    width:350px;
    height:90px ;
    img{
        width: 100%;
    }
}
@media (max-width:660px){
    width:300px;
    height:80px ;
    margin-top:10% ;
    margin-right:-20% ;
    img{
        width: 100%;
    }
}
@media (max-width:1200px){
    width:400px;
    top:15% ;
    img{
        width: 100%;
    }
}
@media (max-width:768px){
    width:300px;
    top:15% ;
    right:0 ;
    height:80px ;
    img{
        width: 100%;
    }
}
@media (max-width:510px){
    width:200px ;
    right:25% ;
    height:80px ;
    top:20% ;
    img{
        width: 100%;
    }
}
@media (max-width:415px){
    width:230px ;
    right:-50% ;
    height:80px ;
    top:20% ;
    img{
        width: 100%;
    }
}
`
export const BodyText=styled.div`
color: white;
z-index: 1;
font-size: 80px;
position: absolute;
left: 15%;
padding: 0;
margin: 0;
top: 40%;
color:#00ff9d;
font-weight: bold;
p{
    display: block;
    margin: 0;
    font-size: 25px;
    color: white;
    @media (max-width:510px){
        font-size:22px ;
    }
}
@media screen and (max-width:1200px){
    top:23% ;
}
@media screen and (max-width:992px){
    top:23% ;
}
@media screen and (max-width:768px){
    top:18% ;
}
@media screen and (max-width:660px){
    top:23% ;
    left:20% ;
}
@media screen and (max-width:510px){
    top:23% ;
    left:10% ;
    
}
@media screen and (max-width:415px){
    top:25% ;
    left:45% ;
    
}
`