import React, { useState } from "react";
import { FooterContainer, FooterWrap } from "./footer";

function Footer() {
  const[FooterIcons,setFooterIcons]=useState(
    {
      icons:false
     
    })
  const[FooterIconsContact,setFooterIconsContact]=useState(
    {
      icons:false
     
    })
  
  const FooterBtnAbout = () => {
    document.getElementById("footerabout").classList.remove("footertext");
    document.getElementById("footerabout").classList.add("shows");
    setFooterIcons({icons:true})
  };
  const FooterBtnAboutTrue = () => {
    document.getElementById("footerabout").classList.remove("shows");
    document.getElementById("footerabout").classList.add("footertext");
    setFooterIcons({icons:false})
  };
  const FooterBtnContact = () => {
    document
      .getElementById("footercontact")
      .classList.remove("footertextcontact");
    document.getElementById("footercontact").classList.add("shows");
    setFooterIconsContact({icons:true})
  };
  const FooterBtnContactTrue = () => {
    document.getElementById("footercontact").classList.remove("shows");
    document.getElementById("footercontact").classList.add("footertextcontact");
    setFooterIconsContact({icons:false})
  };

  return (
    <FooterWrap>
      <link
        rel="stylesheet"
        href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.0.0/css/all.min.css"
        integrity="sha512-9usAa10IRO0HhonpyAIVpjrylPvoDwiPUiKdWk5t3PyolY1cOd4DSE0Ga+ri4AuTroPR5aQvXU9xC6qOPnzFeg=="
        crossorigin="anonymous"
        referrerpolicy="no-referrer"
      />
      <FooterContainer>
        <div>
          <div className="questions">
            <button onClick={FooterIcons.icons==true? FooterBtnAboutTrue:FooterBtnAbout}>
              < span>About</span>
              <i onClick={FooterIcons.icons==true? FooterBtnAboutTrue: FooterBtnAbout}  class={FooterIcons.icons==true? "fa-solid fa-angle-up":'fa-solid fa-angle-down'}></i>
            </button>
            <p className="footertext" id="footerabout">
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Similique
              ea accusamus, mollitia magnam id ais repudiandae debitis a.
            </p>
          </div>
          <div className="questions">
            <button onClick={FooterIconsContact.icons==true? FooterBtnContactTrue:FooterBtnContact}>
              <span>Contact</span>
              <i  onClick={FooterIconsContact.icons==true? FooterBtnContactTrue: FooterBtnContact}  class={FooterIconsContact.icons==true? "fa-solid fa-angle-up":'fa-solid fa-angle-down'}></i>
            </button>
            <p className="footertextcontact" id="footercontact">
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Similique
              ea accusamus, mollitia magnam id a dolorem voluptatum, nihil autem
              aspernatur ipsa quibusdam obcaecati! Accusamus cupiditate unde
              quis repudiandae debitis a.
            </p>
          </div>
        </div>
        <div className="icons-wrapper">
          <div className="border">
            <i class="fa-brands fa-facebook icon"></i>
            <i class="fa-brands fa-instagram icon"></i>
            <i class="fa-brands fa-telegram icon"></i>
          </div>
        </div>
      </FooterContainer>
    </FooterWrap>
  );
}

export default Footer;
